a:link {
  text-decoration: underline;
  color: var(--link-color);
}

a:visited {
  text-decoration: underline;
  color: var(--link-visited-color);
}

a.lowkey:link {
  text-decoration: none;
  color: inherit;
}

a.lowkey:hover {
  text-decoration: underline;
  color: inherit;
}

a.lowkey:visited {
  text-decoration: none;
  color: inherit;
}

/* tip - anchors of class info */
a.tip {
  position:relative;
  z-index:24;
  text-decoration: underline;
}

a.tip:hover {
  z-index:25;
}

a.tip span {
  display: none
}

a.tip:hover span {
  display:block;
  position:absolute;
  top:2em;
  left:2em;
  width:400px;
  border:1px solid #bbbbbb;
  background-color:#fffff0;
  color:#000;
  text-align: left
}

.call-to-action {
  display: table;
  margin-bottom: 5px;
  padding: 10px;
  background-color: var(--alert-info-bg-color-call-out);
  border-left: 4px solid #577baa;
  color: var(--alert-info-color-call-out);
}

.call-to-action a, .call-to-action a:visited {
  color: #000000;
  font-weight: bold;
  font-size: larger;
}

img {
  vertical-align: middle;
  border: 0;
}

div.disabled {
    opacity : 0.4;
    filter: alpha(opacity=40); /* msie */
    background-color: #000;
}

table.tab {
  border-collapse: collapse;
}

td.selected_tab {
  vertical-align: middle;
  border: 1px #090 solid;
  background: #ffffff;
}

td.tab_filler {
  background: #ffffff;
  border-bottom: 1px #090 solid;
}

td.tab {
  vertical-align: middle;
  border: 1px #090 solid;
  background: #f0f0f0;
}

.dashboard td {
  padding: 4px 4px 4px 4px;
}

pre {/* see http://users.tkk.fi/tkarvine/pre-wrap-css3-mozilla-opera-ie.html */
    white-space: pre-wrap;       /* css-3 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    word-wrap: break-word;       /* Internet Explorer 5.5+ */
    margin: 0;
}

pre a {
    word-wrap: break-word;
}

pre.console {
  overflow: auto;
}

.setting-leftspace {
  width: 2em;
}

.setting-name {
  white-space: nowrap;
}

.setting-main {
  width: 100%; /* try to make this column as big as possible. */
}
.setting-help {
  width: 16px;
  vertical-align: middle;
}
.setting-no-help {
  width: 16px;
}

.setting-input {
  width: 100%;
  border-radius: 3px;
  border: 1px solid var(--input-border);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  padding: 6px;
  box-sizing: border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing: border-box;
}

.setting-description {
  font-size: var(--font-size-xs);
  margin-top: 0;
  padding-top: 0;
}

/* tabBar */

.tabBar {
  overflow: auto;
  position: relative;
  top: 1px;
}
.tabBar .tab {
  float: left;
  margin-left: 3px;
}
.tabBar .tab:first-child {
  margin-left: 15px;
}
.tabBar .tab a {
  position: relative;
  display: block;
  border: solid 1px var(--tab-bar-border-color);
  border-bottom: none;
  text-decoration: none;
  color: #999;
  padding: 7px 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.tabBar .tab a:hover {
  background: var(--tab-bar-bg-color--hover);
}
.tabBar .tab a.addTab {
  font-weight: bold;
  color: var(--tab-bar-add-text-color);
  border: 1px solid var(--tab-bar-add-border-color);
  border-bottom: none;
}
.tabBar .tab a.addTab:hover {
  background: var(--tab-bar-add-bg-color--hover);
}
.tabBar .tab [type=radio] {
  display: none;
}
.tabBar [type=radio]:checked ~ a {
  border: solid 1px var(--tab-bar-border-color--checked);
  border-bottom: none;
  background: var(--tab-bar-bg--checked);
  color: var(--tab-bar-text-color--checked);
  font-weight: bold;
  z-index: 2;
}
.tabBarBaseline {
  border-top: 1px solid #f0f0f0;
  z-index: 1;
}

.tabBarBaseline {
  display: none; /* Baseline is hidden by default.  See next rule fo adding visibility. */
}
.tabBarFrame.showBaseline .tabBarBaseline {
  display: block;
}

/* div that looks like a hyperlink */
.pseudoLink {
  cursor: pointer;
}

.advancedLink {
  text-align: right;
}

.advancedBody {
  display: none;
}

.scm_info {
  width: 480px;
}

.build-keep {
  font-weight: bold;
}

.task-header {
  display: block;
  border-bottom: 1px #090 solid;
  font-weight: bold;
  font-size: var(--font-size-xs);
}

.jenkins-not-applicable {
  color: darkgrey;
  font-style: italic;
}

.main-table {
}

/* pane */

.pane-frame {
  border: solid 1px var(--pane-border-color);
  border-radius: 4px;
}

#side-panel .pane-frame:hover, #side-panel .pane-frame.mouseover {
  border: solid 1px var(--panel-border-color--hover);
}

#side-panel .pane-content td.pane {
  font-size: var(--font-size-xs);
}

.pane-header, .pane-footer {
  padding: 8px 0px;
  background-color: var(--pane-header-color);
  border: solid 1px var(--pane-header-border-color);
  border-left: none;
  border-right: none;
  color: var(--pane-text-color);
}
.pane-header {
  border-top: none;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.pane-footer {
  border-bottom: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.pane td {
  padding: 4px 4px 3px 4px;
  vertical-align: middle;
}

table.pane {
  width: 100%;
  border-collapse: collapse;
  border: 1px #bbb solid;
}

td.pane {
  padding: 3px 4px 3px 4px;
  vertical-align: middle;
}

table.stripped tr:nth-of-type(even) {
  background: var(--even-row-color);
}

table.stripped-even tr:nth-child(even) {
  background: var(--even-row-color);
}
table.stripped-odd tr:nth-child(odd) {
  background: var(--even-row-color);
}
table.stripped tr:hover, table.stripped-even tr:hover, table.stripped-odd tr:hover {
  background: var(--table-stripped-bg-color--hover) !important;
}

div.pane-header {
  font-weight: bold;
  padding-right: 24px;
}

div.pane-header .collapse {
  float: right;
  margin-left: 3px;
}

th.pane {
  font-weight: bold;
}

/* executors */

#executors th.pane {
  text-align: left;
  padding: 12px 5px 5px 5px;
}

#executors th.pane a {
  text-decoration: none;
}

/* bigtable */

/* For non-full screen table */
.bigtable.width-auto {
