  }
}

.bigtable td {
  vertical-align: middle;
  padding: 3px 4px 3px 4px;
}

.smallfont {
  font-size: 9px;
}

#foldertab {
  padding: 4px 0;
  margin-left: 0;
  border-bottom: 1px solid #090;
  font: bold 12px Helvetica, Arial, sans-serif;
}

#foldertab li {
  list-style: none;
  margin: 0;
  display: inline;
}

#foldertab li a {
  padding: 4px 0.5em;
  margin-left: 3px;
  border: 1px solid #090;
  border-bottom: none;
  background: #090;
  text-decoration: none;
}

#foldertab li a:link { color: white; }
#foldertab li a:visited { color: white; }

#foldertab li a:hover {
  color: white;
  background: #6c0;
  border-color: #6c0;
}

#foldertab li a#current {
  background: white;
  border-bottom: 1px solid white;
  color: black;
}

.changeset-message {
  border: 1px solid #ccb;
  background: #eed;
  padding: 4px;
  white-space: normal;
}

.disabledJob {
  color: gray;
}

.spinner {
  padding-left: 32px;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  background-image: url("../../images/spinner.gif");
  background-repeat: no-repeat;
  background-position: left;
}

.spinner-right {
  padding-right: 32px;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  background-image: url("../../images/spinner.gif");
  background-repeat: no-repeat;
  background-position: right;
}

#login-field {
    vertical-align: middle;
    padding-right: 1em;
    width:1px;
}

#login-field SPAN {
  white-space:nowrap;
  color: white;
}

#login-field A {
    /*
      link inside login field should be always white.
      If I set this to inherit, it won't work in IE7
     */
    color: white;
}

div.behavior-loading {
    position: absolute;
    left:0; right:0;
    width:100%;
    height:100%;
    background-color: #e4e4e4;
    text-align: center;
    // TODO: use a display font, maybe 3rem
    font-size: 300%;
    opacity: 0.5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
    filter: alpha(opacity=50);
}

.config div.behavior-loading{
    background: rgba(255,255,255,.85);
    left: 0;
    width:100%;
    right: 0;
    top: 15px;
    bottom: 0px;
    height: 100%;
    width: auto;
    min-height: 100%;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    filter: alpha(opacity=100);
    font-size: 1.5em;
    z-index: 99;
    opacity: 1;
    color: #999;
    text-shadow:#fff 0 0 5px,#fff 0 0 5px,#fff 0 0 5px,#fff 0 0 5px,#fff 0 0 5px
}

.behavior-loading:before {
  content:' ';
  display:block;
  width: 100px;
  height: 100px;
  margin: 200px auto 25px;
  background-color: rgba(0,0,0,.15);
  border:5px solid rgba(0,0,0,.33);
  position:relative;
  z-index:2;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out;
  box-shadow:#fff 0 0 0 10px
}

@-webkit-keyframes sk-scaleout {
  0% { -webkit-transform: scale(0) }
  100% {
    -webkit-transform: scale(1.0);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 100% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
    opacity: 0;
  }
}

LABEL.attach-previous {
    margin-left: 0.5em;
}

.bottom-sticker, #bottom-sticker {
