}
.bottom-sticker-inner {
    background: var(--background);
    padding: 1em 0;
}

.top-sticker, #top-sticker {
    width:  100%;   /* it needs to occupy the entire width or else the underlying content will see through */
    z-index: 999;
}
.top-sticker-inner {
    padding:1em;
}
.top-sticker-edge {
    height:16px;
    background-image: url("../../images/top-sticker-bottom-edge.png");
    background-repeat: repeat-x;
}
.top-sticker.noedge > .top-sticker-edge {
    display: none;
}

.top-sticker.noedge > .top-sticker-inner {
    padding:0;
}


/* ======================== error/warning message (mainly in the form.) Use them on block elements ======================== */
.error {
    color: #CC0000;
    font-weight: bold;
    padding-left: 20px;
    min-height: 16px;
    line-height: 16px;
    background-image: url("../../images/16x16/error.png");
    background-position: left center;
    background-repeat: no-repeat;
}

.error-inline {
    color: #CC0000;
    font-weight: bold;
}

.warning {
    color: #C4A000;
    font-weight: bold;
    padding-left: 20px;
    min-height: 16px;
    line-height: 16px;
    background-image: url("../../images/16x16/warning.png");
    background-size: 16px 16px;
    background-position: left center;
    background-repeat: no-repeat;
}

.warning-inline {
    color: #C4A000;
    font-weight: bold;
}

.info {
    color: var(--text-color);
    font-weight: bold;
    padding-left: 20px;
    min-height: 16px;
    background-image: url("../../images/16x16/go-next.png"); /* TODO: get a better icon */
    background-position: left center;
    background-repeat: no-repeat;
}

.icon16x16 {
    width: 16px;
    height: 16px;
}

.icon24x24 {
    width: 24px;
    height: 24px;
}

.icon32x32 {
    width: 32px;
    height: 32px;
}

/* ====================== help ===================================== */

.help {
  display: none;  /* hidden until loaded */
  border: solid #bbb 1px;
  background-color: var(--help-area-bg-color);
  padding: 1em;
  margin-bottom: 1em;
  word-break: break-word;
}

.help .from-plugin {
    text-align: right;
    color: #666;
}

.help-area {
  /* this marker class is used by JavaScript to locate the area to display help text. */
}

.help-button {
  margin-left: 2px;
  margin-right: 2ch;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.setting-help .help-button {
  margin: 0;
}
.icon-help,
.svg-icon.icon-help {
  height: 1.25rem;
  width: 1.25rem;
}
img.icon-help {
  vertical-align: text-top;
}

/* ====================== project view tab bar ===================================== */
#viewList {
  border: none;
  margin-bottom: 0px;
  width: 100%;
  white-space: nowrap;
}
#viewList td {
  padding: 0px;
}
#viewList td.inactive {
  border: solid 1px #ccc;
  border-bottom-color: #bbb;
}
#viewList td.inactive:hover {
  background-color: #777;
}
#viewList td.inactive a {
  text-decoration: none;
  color: #444;
}
#viewList td.inactive a:hover {
  color: #f1f1f1;
}
#viewList td.noleft {
  border-left: none;
}
#viewList td.noright {
  border-right: none;
}
#viewList td.active {
  border: solid 1px #bbb;
  padding: 0.5em;
  border-bottom: none;
  vertical-align:middle;
  background-color: rgb(240,240,240);
  font-weight: bold;
  white-space: nowrap;
}
#viewList td.filler {
  border: none;
  border-bottom: solid 1px #bbb;
  width: 100%;
  text-align: right;
}
#viewList a {
  display: block;
  padding: 0.5em;
  white-space: nowrap;
}

#projectstatus .header {
